// Typography of project
@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,500;1,500&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Rubik:ital@0;1&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Rubik:ital,wght@0,300;1,300&display=swap');

@mixin typeface-1($size, $device: desktop){

    font-family: 'Rubik', sans-serif;
    
    // H0
    @if $size == h0 and $device == desktop{
        font-size: 120px;
        line-height: 120%;
        font-weight: 500;
    }@else if $size == h0 and $device == mobile{
        font-size: 88px;
        line-height: 120%;
        font-weight: 500;
    }@else if $size == h0 and $device == smallmobile{
        font-size: 32px;
        line-height: 120%;
        font-weight: 500;
    }

    // H1
    @if $size == h1 and $device == desktop{
        font-size: 80px;
        line-height: 100%;
        font-weight: 500;
    }@else if $size == h1 and $device == mobile{
        font-size: 56px;
        line-height: 110%;
        font-weight: 500;
    }@else if $size == h1 and $device == smallmobile{
        font-size: 48px;
        line-height: 120%;
        font-weight: 500;
    }

    // H2
    @else if $size == h2 and $device == desktop{
        font-size: 64px;
        line-height: 110%;
        font-weight: 300;
    }@else if $size == h2 and $device == mobile{
        font-size: 40px;
        line-height: 120%;
        font-weight: 300;
    }@else if $size == h2 and $device == smallmobile{
        font-size: 36px;
        line-height: 120%;
        font-weight: 300;
    }

    // H3
    @else if $size == h3 and $device == desktop{
        font-size: 48px;
        line-height: 120%;
        font-weight: 500;
    }@else if $size == h3 and $device == mobile{
        font-size: 32px;
        line-height: 120%;
        font-weight: 500;
    }@else if $size == h3 and $device == smallmobile{
        font-size: 28px;
        line-height: 130%;
        font-weight: 300;
    }
    
    // H4
    @else if $size == h4 and $device == desktop{
        font-size: 36px;
        line-height: 120%;
        font-weight: 400;
    }@else if $size == h4 and $device == mobile{
        font-size: 28px;
        line-height: 130%;
        font-weight: 400;
    }@else if $size == h4 and $device == smallmobile{
        font-size: 20px;
        line-height: 140%;
        font-weight: 300;
    }
    
    // H5
    @else if $size == h5 and $device == desktop{
        font-size: 24px;
        line-height: 140%;
        font-weight: 300;
    }@else if $size == h5 and $device == mobile{
        font-size: 20px;
        line-height: 140%;
        font-weight: 300;
    }
    
    @else if $size == h6 and $device == desktop{
        font-size: 18px;
        line-height: 150%;
        font-weight: 100;
    }@else if $size == h6 and $device == mobile{
        font-size: 16px;
        line-height: 150%;
        font-weight: 100;
    }

    @else if $size == h7 and $device == desktop{
        font-size: 14px;
        line-height: 150%;
        font-weight: 100;
    }@else if $size == h7 and $device == mobile{
        font-size: 14px;
        line-height: 150%;
        font-weight: 100;
    }

}


@mixin typeface-2($size: p2, $device: desktop){

    font-family: 'Rubik', sans-serif;

    // P1
    @if $size == p1 and $device == desktop{
        font-size: 24px;
        line-height: 30px;
        font-weight: 300;
    }@else if $size == p1 and $device == mobile{
        font-size: 22px;
        line-height: 30px;
        font-weight: 300;
    }@else if $size == p1 and $device == smallmobile{
        font-size: 20px;
        line-height: 24px;
        font-weight: 300;
    }

    // P2
    @if $size == p2 and $device == desktop{
        font-size: 20px;
        line-height: 30px;
        font-weight: 200;
    }@else if $size == p2 and $device == mobile{
        font-size: 18px;
        line-height: 26px;
        font-weight: 200;
    }@else if $size == p2 and $device == smallmobile{
        font-size: 16px;
        line-height: 26px;
        font-weight: 200;
    }

    // P3
    @if $size == p3 and $device == desktop{
        font-size: 16px;
        line-height: 26px;
        font-weight: 200;
    }@else if $size == p3 and $device == mobile{
        font-size: 14px;
        line-height: 26px;
        font-weight: 200;
    }@else if $size == p3 and $device == smallmobile{
        font-size: 14px;
        line-height: 26px;
        font-weight: 200;
    }

    // P4
    @if $size == p4 and $device == desktop{
        font-size: 14px;
        line-height: 24px;
        font-weight: 200;
    }@else if $size == p4 and $device == mobile{
        font-size: 12px;
        line-height: 20px;
        font-weight: 200;
    }

    // P5
    @if $size == p5 and $device == desktop{
        font-size: 12px;
        line-height: 24px;
        font-weight: 200;
    }@else if $size == p5 and $device == mobile{
        font-size: 10px;
        line-height: 20px;
        font-weight: 200;
    } 
}

@import 'layout';

.h0{
    @include typeface-1(h0, desktop);
    @include responsive(s)
    {
        @include typeface-1(h0, mobile);
    }
    @include responsive(xs)
    {
        @include typeface-1(h0, smallmobile);
        
    }
}
h1 {
    color: $white-dark;
    z-index: 5;

    @include typeface-1(h1, desktop);
    @include responsive(s) 
    {
        @include typeface-1(h1, mobile);        
    }
    @include responsive(xs) 
    {
        @include typeface-1(h1, smallmobile);
    }
}
